import styled, { css } from "styled-components";
import { Row, Column } from "simple-flexbox";
import { CopyToClipboard } from "react-copy-to-clipboard";
import React, { useState } from "react";
import {
  socialMediaLinks
} from "../../constants";

const Container = styled.div`
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  background-color: #191d43;
  flex-flow: wrap;
`;
const Image = styled.img`
  padding: 10px;
  width: 200px;
  height: 50px;
  margin-left: 45px;
  margin-top: 3px;
`;
const MobileImage = styled.img`
  padding: 10px;
  width: 200px;
  height: 50px;
  margin-left: 12px;
  margin-top: 3px;
`;
const Span = styled.span`
  color: #ffffff;
  font-size: 21px;
  margin-top: 21px;
  font-weight: 600;
  font-family: "Raleway", sans-serif !important;
  margin-left: -11px;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.19;
`;
const Input = styled.input`
  background-color: #3d4270;
  border-radius: 4px;
  color: #ffffff;
  font-family: WorkSans-Roman !important;
  font-size: 15px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
  text-align: left;
  border: none;
  height: 32px;
  width: 420px;
  margin-left: 25px;
  margin-top: 11px;
  padding-left: 20px;
  @media(min-width: 0px) and (max-width: 767px){
    width: 230px;
  }
  @media(min-width: 768px) and (max-width: 1024px){
    width: 300px;
  }
`;
const Button = styled.button`
  color: #ffffff;
  height: 32px;
  width: 96px;
  padding: 10px;
  margin-top: 11px;
  margin-left: 10px;
  border: none;
  border-radius: 4px;
  background-color: #5760bc;
  font-size: 18px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 0.28;
  font-family: "IBM Plex Sans", sans-serif !important;
  &:hover {
    color: black;
  }
  @media(min-width: 0px) and (max-width: 767px){
    width: 67px;
  }
  @media(min-width: 768px) and (max-width: 1024px){
    width: 67px;
  }
`;

const SpeedButton = styled.button`
  color: white;
  background-color: transparent;
  border: 0.5px solid #ffffff;
  overflow: hidden;
  padding: 4px 20px;
  border-radius: 4px;
  cursor: pointer;
  &:hover {
    background-color: grey;
  }
`;

const DesktopButtonContainer = styled.div`
 padding: 9px 60px 5px 0;
 @media (min-width: 768px) and (max-width: 880px) {
    padding: 9px 0px 9px 0;
    margin-right : auto;
    margin-left: auto;

  }
`;

const MobileButtonContainer = styled.div`
  margin-left: auto;
  margin-right: auto;
`;

const DesktopView = styled.div`
  @media (min-width: 0px) and (max-width: 767px) {
    display: none;
  }
  @media (min-width: 768px) {
    display: visible;
  }
`;

const MobileView = styled.div`
  @media (min-width: 0px) and (max-width: 767px) {
    display: visible;
  }
  @media (min-width: 768px) {
    display: none;
  }
`;
export default function HeaderComponent(props) {
  const [inputValue, setInputValue] = useState("");
  // console.log('pr---',props?.headerLink)

  return (
    <>
      <DesktopView>
        <Container>
          <Row>
            <a href="/">
              <Image src="/images/archiveLogo.png" />
            </a>

            <Input
              type="text"
              readOnly={true}
              value={props?.headerLink || ""}
              onChange={(e) => setInputValue(e.target.value)}
            />
            <CopyToClipboard text={props?.headerLink}>
              <Button>Copy</Button>
            </CopyToClipboard>
          </Row>
          <DesktopButtonContainer>
            <a href={socialMediaLinks.XDC_SPEED_TEST_LINK} target="_blank">
              <SpeedButton>XDC Speed Test</SpeedButton>
            </a>
          </DesktopButtonContainer>
        </Container>
      </DesktopView>

      <MobileView>
        <Container>
          <Row>
            <a href="/">
              <MobileImage src="/images/archiveLogo.png" />
            </a>
          </Row>
          <Column style={{ padding: "0 30px 20px 0" }}>
            <Row>
              <Input
                type="text"
                readOnly={true}
                value={props?.headerLink || ""}
                onChange={(e) => setInputValue(e.target.value)}
              />
              <CopyToClipboard text={props?.headerLink}>
                <Button>Copy</Button>
              </CopyToClipboard>
            </Row>
          </Column>
          <MobileButtonContainer>
            <Row className="mobile-button-row">
              <a href={socialMediaLinks.XDC_SPEED_TEST_LINK} target="_blank">
                <SpeedButton>XDC Speed Test</SpeedButton>
              </a>
            </Row>
          </MobileButtonContainer>
        </Container>
      </MobileView>
    </>
  );
}
